<template>
  <div v-if="Auth.getPerfil() != config.PERFIL_CONTADOR">
    <div
      id="b-toaster-bottom-full"
      class="b-toaster b-toaster-bottom-full mobile-item"
      v-show="item"
    >
      <div class="b-toaster-slot vue-portal-target">
        <div
          data-v-fae5bece
          id="my-toast__toast_outer"
          role="alert"
          aria-live="assertive"
          aria-atomic="true"
          class="b-toast b-toast-prepend"
        >
          <div tabindex="0" id="my-toast" class="toast">
            <header class="toast-header bar-item">
              <!-- <div data-v-fae5bece class="d-flex flex-grow-1 align-items-baseline">
               
                <img
                  data-v-fae5bece
                  src="data:image/svg+xml;charset=UTF-8,%3Csvg%20width%3D%2212%22%20height%3D%2212%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20%25%7Bw%7D%20%25%7Bh%7D%22%20preserveAspectRatio%3D%22none%22%3E%3Crect%20width%3D%22100%25%22%20height%3D%22100%25%22%20style%3D%22fill%3A%23ff5555%3B%22%3E%3C%2Frect%3E%3C%2Fsvg%3E"
                  width="12"
                  height="12"
                  class="mr-2"
                />
                <strong  class="mr-auto ">EXPERIMENTE GRÁTIS!!</strong>
               
              </div>-->
              <span class="mr-5 ml-auto" style="font-size:23px;"
                >Existe Débito em Aberto{{ dataInicial }}</span
              >
              <span>
                <b-button
                  style="width: 100%!important"
                  pill
                  variant="success"
                  @click="$router.push('/pagarAssinatura')"
                  size="lg"
                  >PAGAR</b-button
                >
              </span>

              <button
                @click.prevent="esconder()"
                class="close ml-auto mb-1"
                style="color:white"
              >
                ×
              </button>
            </header>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Auth from "../../auth";
import axios from "axios";
import config from "../../config";
import Swal from "sweetalert2";
import moment from "moment";

export default {
  data() {
    return {
      config,
      Auth,
      item: false,
      dataInicial: null,
    };
  },
  created: function() {
    this.buscarCaixa(5);
  },
  methods: {
    esconder() {
      this.item = false;
      Swal.fire({
        icon: "warning",
        title: "Oops...",
        text: "Há boletos em aberto! ",
      });
      return (this.item = true);
    },
    async buscarCaixa(num) {
      var data;
      var dataFinal;
      var dataInicial;
      if (num == 5) {
        // var dataInicial = moment().format("l");
        dataInicial = this.dataInicial;
        dataFinal = this.dataFinal;

        this.codigoEmpresa = Auth.getUserInfo().codigoEmpresa;

        data = {
          codigoEmpresa: this.codigoEmpresa,
          dataInicial: dataInicial,
          dataFinal: dataFinal,
          nome: " ",
          cpF_CNPJ: " ",
        };
      }

      try {
        var response = await axios.post(
          `${config.API_URL}/Pagamento/Pesquisar`,
          data
        );

        var vencimento;
        var valor;
        var caixas = response.data.pagamentos;
        valor = moment().format("L");

        for (let index = 0; index < caixas.length; index++) {
          vencimento = moment(caixas[index].dataFechamento).format(
            "YYYY-MM-DD"
          );
          dataInicial = moment(valor).format("YYYY-MM-DD");
          if (caixas[index].status == "PENDENTE" && vencimento < dataInicial) {
            this.item = true;
            break;
          }
        }
      } catch (error) {
        return error;
      }
    },
    myFunction() {
      setInterval(this.alertFunc(), 3000);
    },

    alertFunc() {
      alert("Hello!");
    },
  },
};
</script>
<style scoped>
.bar-item {
  background: rgb(255, 152, 0);
  background: linear-gradient(
    90deg,
    rgba(255, 152, 0, 1) 63%,
    rgba(255, 86, 0, 1) 99%
  );
  text-align: center !important;
  color: white !important;
}
</style>
