<template>
  <div>
    <base-nav
      class="navbar-top navbar-dark"
      id="navbar-main"
      :show-toggle-button="false"
      expand
    >
      <form
        class="navbar-search navbar-search-dark form-inline mr-3 d-none d-md-flex ml-lg-auto"
      >
        <div class="form-group mb-0">
          <!-- <base-input placeholder="Search"
                            class="input-group-alternative"
                            alternative=""
                            addon-right-icon="fas fa-search">
        </base-input>-->
        </div>
      </form>
      <ul class="navbar-nav align-items-center d-none d-md-flex">
        <li class="nav-item dropdown">
          <base-dropdown class="nav-link pr-0">
            <div class="media align-items-center" slot="title">
              <!-- <span class="avatar avatar-sm rounded-circle">
                  <img alt="Image placeholder" src="img/theme/team-4-800x800.jpg">
            </span>-->
              <div class="media-body ml-2 d-none d-lg-block">
                <span class="mb-0 text-sm font-weight-bold"
                  >Olá, {{ Auth.getUserInfo().nome }}</span
                >
              </div>
              <i class="ni ni-bell-55 ml-3 "></i>
            </div>

            <template>
              <div class="dropdown-header noti-title">
                <h6 class="text-overflow m-0">Bem vindo!</h6>
              </div>
              <router-link to="/" class="dropdown-item">
                <i class="ni ni-shop"></i>
                <span>Home</span>
              </router-link>
              <router-link
                v-if="Auth.getPerfil() != config.PERFIL_CONTADOR"
                to="/planosPrincipal"
                class="dropdown-item"
              >
                <i class="ni ni-credit-card"></i>
                <span>Planos</span>
              </router-link>
              <router-link
                v-if="Auth.getPerfil() != config.PERFIL_CONTADOR"
                to="/minhaAssinatura"
                class="dropdown-item"
              >
                <i class="ni ni-money-coins"></i>
                <span>Assinatura</span>
              </router-link>
              <router-link to="/ajuda" class="dropdown-item">
                <i class="ni ni-atom"></i>
                <span>Ajuda</span>
              </router-link>
              <div class="dropdown-divider"></div>
              <router-link
                v-if="Auth.getPerfil() == config.PERFIL_ADMIN"
                to="/painelAdminPDV"
                class="dropdown-item"
              >
                <i class="ni ni-money-coins"></i>
                <span>Empresa</span>
              </router-link>
              <a
                href
                @click.prevent="abrirModal"
                v-if="Auth.getFiliais()"
                class="dropdown-item"
              >
                <i class="ni ni-folder-17"></i>
                <span>Trocar Empresa</span>
              </a>

              <a href @click.prevent="onClickLogout" class="dropdown-item">
                <i class="ni ni-button-power"></i>
                <span>Sair</span>
              </a>
            </template>
          </base-dropdown>
        </li>
      </ul>
    </base-nav>

    <section>
      <div>
        <!-- <b-button @click="modalShow = !modalShow">Open Modal</b-button> -->

        <b-modal
          v-model="modalShow1"
          size="lg"
          :hide-footer="true"
          centered
          title="Acessar outra filial  "
          header-bg-variant="yellow"
          class="mobile-item"
          style="color:white!important"
        >
          <div class="modal-titulo text-center" style="font-family:Poppins">
            <h2>Filial</h2>
          </div>
          <div class="modal-titulo text-center mb-5" style="font-size:20px">
            <div class="mt-5">
              <b-form-select v-model="selected" @change="trocarFilial">
                <option :value="null" disabled>Escolha uma conta</option>
                <option
                  v-for="(item, index) in Auth.getFiliais()"
                  :key="item.nome"
                  :value="index"
                  >{{ item.nome }} - {{ item.cpf_cnpj }}</option
                >
              </b-form-select>
            </div>
          </div>
        </b-modal>
      </div>
    </section>
  </div>
</template>
<script>
import Auth from "../auth";
import config from "../../src/config";
import Swal from "sweetalert2";
import axios from "axios";
// Charts
export default {
  data() {
    return {
      Auth: Auth,
      config,
      activeNotifications: false,
      showMenu: false,
      searchQuery: "",
      modalShow1: false,
      selected: null,
    };
  },
  methods: {
    toggleSidebar() {
      this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
    },
    hideSidebar() {
      this.$sidebar.displaySidebar(false);
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    },
    onClickLogout() {
      Auth.logout();
      this.$router.replace("/login");
    },
    abrirModal() {
      this.modalShow1 = true;
    },
    trocarFilial() {
      if (Auth.getFiliais()) {
        for (let index = 0; index < Auth.getFiliais().length; index++) {
          if (this.selected == index) {
            var userInfo = Auth.getFiliais()[index];
            var v = JSON.stringify(userInfo);
            window.localStorage.setItem("UserInfo", v);
            continue;
          }
        }
        this.planoComprado();
      }
    },
    async planoComprado() {
      Swal.fire({
        position: "center",
        icon: "success",
        title: "Troca efetuada com sucesso!",
        showConfirmButton: false,
        timer: 9000,
      });

      try {
        var t = Auth.getUserInfo().cpf_cnpj;

        var data = {
          cpF_CNPJ: t,
        };

        var response = await axios.post(
          `${config.API_URL}/Assinatura/BuscarAssinaturaEmpresa`,
          data
        );

        var assinaturaEmpresa = response.data.assinaturaEmpresa;

        if (assinaturaEmpresa) {
          var c = JSON.stringify(assinaturaEmpresa);
          window.localStorage.setItem("AssinaturaEmpresa", c);
        }

        window.location.reload();
      } catch (error) {
        console.log(error);
      }
    },
  },
};
</script>
