import moment from "moment";
import axios from "axios";

export default {
  isLoggedIn() {
    var token = this.getAuthToken();
    var dtExpiry = this.getExpiryDate();
    var assinaturaEmpresa = this.getAssinatura();

    if (!token) {
      return false;
    }

    //Verifica se o token ainda não expirou
    if (token && dtExpiry && moment().fromNow() > dtExpiry) {
      return false;
    }
    if (!assinaturaEmpresa) {
      return false;
    }

    //Atribui o token como Header padrão de todas as requisições
    axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;

    return true;
  },
  getFiliais() {
    var value = [];
    value = JSON.parse(localStorage.getItem("Filiais"));
    return value;
  },

  getAuthToken() {
    var value = window.localStorage.getItem("AuthToken");
    return value;
  },

  getUserInfo() {
    var userInfo = {};

    var value = window.localStorage.getItem("UserInfo");
    if (value) {
      try {
        userInfo = JSON.parse(value);
      } catch (e) {
        console.error(e);
      }
    }
    return userInfo;
  },
  getAssinatura() {
    var assinaturaEmpresa = {};

    var value = window.localStorage.getItem("AssinaturaEmpresa");
    if (value) {
      try {
        assinaturaEmpresa = JSON.parse(value);
      } catch (e) {
        console.error(e);
      }
    }
    return assinaturaEmpresa;
  },
  getPerfil() {
    var idPerfil = {};

    var value = window.localStorage.getItem("IdPerfil");
    if (value) {
      try {
        idPerfil = JSON.parse(value);
      } catch (e) {
        console.error(e);
      }
    }
    return idPerfil;
  },
  getUserName() {
    var nome = this.getUserInfo().nome || "Usuário";

    //Exibindo apenas o primeiro nome
    return nome.split(" ")[0];
  },

  getExpiryDate() {
    var value = window.localStorage.getItem("DtExpiry");
    return parseInt(value);
  },

  login(token, dtExpiry, userInfo, assinaturaEmpresa, filiais) {
    if (token) {
      window.localStorage.setItem("AuthToken", token);
      axios.defaults.headers.common["Authorization"] = `Bearer ${token}`;
    }

    if (dtExpiry) {
      var expiryTime = dtExpiry;

      window.localStorage.setItem("DtExpiry", expiryTime);
    }

    if (userInfo) {
      var v = JSON.stringify(userInfo);
      window.localStorage.setItem("UserInfo", v);

      if (userInfo.idPerfil) {
        var i = JSON.stringify(userInfo.idPerfil);
        window.localStorage.setItem("IdPerfil", i);
      }
    }

    if (assinaturaEmpresa) {
      var c = JSON.stringify(assinaturaEmpresa);
      window.localStorage.setItem("AssinaturaEmpresa", c);
    }
    if (filiais) {
      var f = JSON.stringify(filiais);
      window.localStorage.setItem("Filiais", f);
    }
  },

  logout() {
    window.localStorage.removeItem("AuthToken");
    axios.defaults.headers.common["Authorization"] = "";

    window.localStorage.removeItem("DtExpiry");
    window.localStorage.removeItem("UserInfo");
    window.localStorage.removeItem("AssinaturaEmpresa");
    window.localStorage.removeItem("Filiais");
    window.localStorage.removeItem("IdPerfil");
  },
};
