            </router-link>
            <div class="dropdown-divider"></div>
            <router-link
              v-if="Auth.getPerfil() == config.PERFIL_ADMIN"
              to="/painelAdminPDV"
              class="dropdown-item"
            >
              <i class="ni ni-box-2"></i>
              <span>Empresa</span>
            </router-link>
            <a
