            >
              <i class="ni ni-folder-17"></i>
              <span>Trocar Empresa</span>
            </a>
            <a href @click.prevent="onClickLogout" class="dropdown-item">
              <i class="ni ni-button-power"></i>
              <span>Sair</span>
            </a>
          </base-dropdown>
        </ul>
      </slot>
      <slot></slot>
      <div
        v-show="$sidebar.showSidebar"
        class="navbar-collapse collapse show"
        id="sidenav-collapse-main"
      >
        <div class="navbar-collapse-header d-md-none">
          <div class="row">
            <div class="col-6 collapse-brand">
              <router-link to="/">
                <img :src="logo" />
              </router-link>
            </div>
            <div class="col-6 collapse-close">
              <navbar-toggle-button
                @click.native="closeSidebar"
              ></navbar-toggle-button>
            </div>
          </div>
        </div>

        <ul
          class="navbar-nav"
          v-if="Auth.getPerfil() != config.PERFIL_CONTADOR"
        >
          <slot name="links"></slot>
        </ul>
        <!--Divider-->
        <div v-if="Auth.getPerfil() == config.PERFIL_ADMIN">
          <hr class="my-3" />
          <!--Heading-->
          <h6 class="navbar-heading text-muted">Relatorio PDV</h6>
          <!--Navigation-->
          <ul class="navbar-nav mb-md-3">
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Empresa',
                  icon: 'ni ni-briefcase-24 text-warning',
