                :link="{
                  name: 'Assinaturas',
                  icon: 'ni ni-briefcase-24 text-warning',
                  path: '/painelAssinaturaPDV',
                }"
              />
            </li>
          </ul>
        </div>
        <!--Divider-->
        <div v-if="Auth.getPerfil() != config.PERFIL_CONTADOR">
          <hr class="my-3" />
          <!--Heading-->
          <h6 class="navbar-heading text-muted">Relatorios Venda</h6>
          <!--Navigation-->
          <ul class="navbar-nav mb-md-3">
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Vendas',
                  icon: 'ni ni-money-coins text-success',
                  path: '/vendasDashBoard',
                }"
              />
            </li>
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Item Vendido',
                  icon: 'ni ni-money-coins text-success',
                  path: '/itensVendidosDashBoard',
                }"
              />
            </li>
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Item Cancelado',
                  icon: 'ni ni-money-coins text-danger',
                  path: '/itensCanceladosDashBoard',
                }"
              />
            </li>
          </ul>
        </div>
        <div
          v-if="
            Auth.getPerfil() == config.PERFIL_CONTADOR ||
              Auth.getPerfil() == config.PERFIL_ADMIN
          "
        >
          <hr class="my-3" />
          <!--Heading-->
          <h6 class="navbar-heading text-muted">Notas Fiscais</h6>
          <!--Navigation-->
          <ul class="navbar-nav mb-md-3">
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Notas Fiscais',
                  icon: 'ni ni-money-coins text-success',
                  path: '/notaFiscalDashBoard',
                }"
              />
            </li>
          </ul>
        </div>
        <!--Divider-->
        <div v-if="Auth.getPerfil() != config.PERFIL_CONTADOR">
          <hr class="my-3" />
          <!--Heading-->

          <h6 class="navbar-heading text-muted">Relatorios Caixa</h6>
          <!--Navigation-->
          <ul class="navbar-nav mb-md-3">
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Caixa',
                  icon: 'ni ni-app text-blue',
                  path: '/caixaDashBoard',
                }"
              />
            </li>
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Recebimento',
                  icon: 'ni ni-app text-blue',
                  path: '/recebimentos',
                }"
              />
            </li>
          </ul>
        </div>
        <div v-if="Auth.getPerfil() != config.PERFIL_CONTADOR">
          <hr class="my-3" />
          <!--Heading-->
          <h6 class="navbar-heading text-muted">Relatorios Produto</h6>
          <!--Navigation-->
          <ul class="navbar-nav mb-md-3">
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Produto',
                  icon: 'ni ni-bag-17 text-success',
                  path: '/ProdutosDashBoard',
                }"
              />
            </li>
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Estoque',
                  icon: 'ni ni-bag-17 text-primary',
                  path: '/EstoqueProdutoDashBoard',
                }"
              />
            </li>
          </ul>
        </div>
        <div v-if="Auth.getPerfil() != config.PERFIL_CONTADOR">
          <hr class="my-3" />
          <!--Heading-->
          <h6 class="navbar-heading text-muted">Relatorios Pessoa</h6>
          <!--Navigation-->
          <ul class="navbar-nav mb-md-3">
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Cliente',
                  icon: 'ni ni-single-02 text-success',
                  path: '/ClientesDashBoard',
                }"
              />
            </li>
            <li class="nav-item">
              <sidebar-item
                :link="{
                  name: 'Colaborador',
                  icon: 'ni ni-circle-08 text-primary',
                  path: '/ColaboradorDashBoard',
                }"
              />
            </li>
            <li class="nav-item">
              <a class="nav-link" href @click="onClickLogout">
                <i class="ni ni-button-power text-danger"></i> sair
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <section>
      <div>
        <!-- <b-button @click="modalShow = !modalShow">Open Modal</b-button> -->

        <b-modal
          v-model="modalShow1"
          size="lg"
          :hide-footer="true"
          centered
          title="Acessar outra filial"
          header-bg-variant="yellow"
          class="mobile-item"
          style="color: white !important"
        >
          <div class="modal-titulo text-center" style="font-family: Poppins">
            <h2>Filiais</h2>
          </div>
          <div class="modal-titulo text-center mb-5" style="font-size: 20px">
            <div class="mt-5">
              <b-form-select v-model="selected" @change="trocarFilial">
                <option value disabled selected>Escolha uma conta</option>
                <option
                  v-for="(item, index) in Auth.getFiliais()"
                  :key="item.nome"
                  :value="index"
                >
                  {{ item.nome }} - {{ item.cpf_cnpj }}
                </option>
              </b-form-select>
            </div>
          </div>
        </b-modal>
      </div>
    </section>
  </nav>
</template>
<script>
import NavbarToggleButton from "@/components/NavbarToggleButton";
import Auth from "../../auth";
import config from "../../config";
import Swal from "sweetalert2";
import axios from "axios";

export default {
  name: "sidebar",
  components: {
    NavbarToggleButton,
  },
  props: {
    logo: {
      type: String,
      default: "img/brand/green.png",
      description: "Sidebar app logo",
    },
    autoClose: {
      type: Boolean,
      default: true,
      description:
        "Whether sidebar should autoclose on mobile when clicking an item",
    },
  },
  provide() {
    return {
      autoClose: this.autoClose,
    };
  },
  data() {
    return {
      Auth: Auth,
      config,
      modalShow1: false,
      selected: null,
    };
  },
  methods: {
    onClickLogout() {
      Auth.logout();
      alert(this.modalShow1);
      this.$router.replace("/login");
    },
    closeSidebar() {
      this.$sidebar.displaySidebar(false);
    },
    showSidebar() {
      this.$sidebar.displaySidebar(true);
    },
    abrirModal() {
      this.modalShow1 = true;
    },
    trocarFilial() {
      if (Auth.getFiliais()) {
        for (let index = 0; index < Auth.getFiliais().length; index++) {
          if (this.selected == index) {
            var userInfo = Auth.getFiliais()[index];
            var v = JSON.stringify(userInfo);
            window.localStorage.setItem("UserInfo", v);
            continue;
          }
        }
        this.planoComprado();
      }
    },
    async planoComprado() {
      Swal.fire({
        position: "center",
        icon: "success",
        title: "Troca efetuada com sucesso!",
        showConfirmButton: false,
        timer: 9000,
      });

      try {
        var t = Auth.getUserInfo().cpf_cnpj;

        var data = {
          cpF_CNPJ: t,
        };

        var response = await axios.post(
          `${config.API_URL}/Assinatura/BuscarAssinaturaEmpresa`,
          data
        );

        var assinaturaEmpresa = response.data.assinaturaEmpresa;

        if (assinaturaEmpresa) {
          var c = JSON.stringify(assinaturaEmpresa);
          window.localStorage.setItem("AssinaturaEmpresa", c);
        }

        window.location.reload();
      } catch (error) {
        console.log(error);
      }
    },
  },

  beforeDestroy() {
    if (this.$sidebar.showSidebar) {
      this.$sidebar.showSidebar = false;
    }
  },
};
</script>
